/* Following code has been commented with appropriate comments for your reference.
*/
/* Styles for the container holding the profile information */
.profile-container {
    max-width: 400px; /* Set the maximum width of the container to 400 pixels */
    margin-top: 5% ; /* Center the container horizontally with a 15% top margin */
    margin-left: 30%;
    padding: 1.25rem; /* Add 20 pixels of padding inside the container */
    background-color: #fff; /* Set the background color of the container to light gray */
    border-radius: 4px; /* Apply a border radius of 4 pixels to round the corners */
    align-items: center;
    justify-content: center;
    box-shadow: 0px 6px 16px 0px rgba(0,0,0,0.2);
}
/* Styles for the heading within the profile container */
.profile-container h1 {
    margin-top: 0; /* Remove any top margin from the heading */
}
/* Styles for the form within the profile container */
.profile-container form {
    margin-bottom: 20px; /* Add a bottom margin of 20 pixels to the form */
    margin-right: 30px;
}
/* Styles for labels within the profile container */
.profile-container label {
    display: block; /* Display labels as block elements to stack them vertically */
    margin-bottom: 10px; /* Add a bottom margin of 10 pixels to separate labels */
}
/* Styles for text and email input fields within the profile container */
.profile-container input[type="text"],
.profile-container input[type="email"] {
    width: 100%; /* Set the width of text and email inputs to 100% of the container */
    padding: 8px; /* Add 8 pixels of padding inside the input fields */
    border: 1px solid #ccc; /* Apply a 1 pixel solid border with light gray color */
    border-radius: 4px; /* Round the corners of the input fields with a border radius of 4 pixels */
}
/* Styles for buttons within the profile container */
.profile-container button {
    display: block; /* Display buttons as block elements to make them full width */    
    width: 80%; /* Set the width of buttons to 100% of the container */
    padding: 0.8rem; /* Add 8 pixels of padding inside the buttons */
    background-color: #2190ff; /* Set the background color of buttons to a blue shade */
    color: #fff; /* Set the text color of buttons to white */
    border: none; /* Remove borders from buttons */
    border-radius: 4px; /* Apply a border radius of 4 pixels to round button corners */
    cursor: pointer; /* Change cursor to pointer when hovering over buttons */
    
}
/* Styles for the container to edit  profile details */
.profile-details {
    background-color: #fff; /* Set the background color of details container to white */
    border-radius: 0.7rem; /* Apply a border radius of 4 pixels to round the corners */
    padding: 0px; /* Add 20 pixels of padding inside the details container */
}
/* Styles for headings within the profile details container */
.profile-details h2 {
    margin-top: 0; /* Remove any top margin from headings inside details container */
}
/* Styles for paragraphs within the profile details container */
.profile-details p {
    margin-bottom: 0.5rem; /* Add a bottom margin of 10 pixels to separate paragraphs */
    margin-top:0.25rem;
}
/* Styles for buttons within the profile details container */
.profile-details button {
    background-color: #2190ff; /* Set the background color of buttons to a blue shade */
    color: #fff; /* Set the text color of buttons to white */
    border: none; /* Remove borders from buttons */
    border-radius: 4px; /* Apply a border radius of 4 pixels to round button corners */
    padding: 8px 12px; /* Add padding inside buttons (8 pixels top/bottom, 12 pixels left/right) */
    cursor: pointer; /* Change cursor to pointer when hovering over buttons in details container */
    
}